import { min } from "./number";

export interface IArrayDiff<T> {
  deleted: T[];
  added: T[];
  keep: T[];
}
/**
 * 
 * @param oData ()
 * @param nData 
 * @param getValue key
 * @returns
 */
export function findArrayDiff<T, K>(
  oData: T[],
  nData: T[],
  getValue: (v: T) => K = (v) => v as any
) {
  const oMap = new Map<K, T>();
  oData.forEach((d) => oMap.set(getValue(d), d));
  const result: IArrayDiff<T> = {
    deleted: [],
    added: [],
    keep: [],
  };
  nData.forEach((d) => {
    const v = getValue(d);
    if (!oMap.has(v)) {
      result.added.push(d);
    } else {
      result.keep.push(d);
    }
    oMap.delete(v);
  });
  oMap.forEach((d) => result.deleted.push(d));
  return result;
}

/**
 * 
 * @param size 
 * @param gene 
 * @returns
 */
export function generateArray<T>(size: number, gene: (index: number) => T) {
  const arr: T[] = [];
  for (let i = 0; i < size; i++) {
    arr.push(gene(i));
  }
  return arr;
}

/**
 * 
 * @param nodes 
 * @param param1 
 * @returns
 */
export function generateTreeByArray<T, K = T>(
  nodes: T[],
  {
    id = "id",
    children = "children",
    parent = "parent_id",
  }: { children?: string; id?: string; parent?: string }
): K[] {
  const res: K[] = [];
  const map = new Map<any, any>();
  const cache = new Map<any, any[]>();
  for (let i in nodes) {
    const node: any = nodes[i];
    //  
    if (cache.has(node[id])) {
      node[children] = cache.get(node[id]);
      //  
      cache.delete(node[id]);
    }
    // 
    const parentId = node[parent];
    if (!parentId) {
      res.push(node);
    } else {
      // 
      if (map.has(parentId)) {
        const parentNode = map.get(parentId);
        if (!parentNode[children]) {
          parentNode[children] = [];
        }
        parentNode[children].push(node);
      } else {
        // map
        if (!cache.has(parentId)) {
          cache.set(parentId, []);
        }
        // 
        cache.get(parentId)?.push(node);
      }
    }
    map.set(node[id], node);
  }
  return res;
}

/**
 * map
 * @param arr 
 * @param getKey itemkey
 * @returns
 */
export function toMap<T, K>(arr: T[], getKey: (i: T) => K) {
  const map = new Map<K, T>();
  arr.forEach((i) => {
    const key = getKey(i);
    map.set(key, i);
  });
  return map;
}

/**
 * size
 * @param arr 
 * @param size 
 * @returns
 */
export function chunk<T>(arr: T[], size: number) {
  const res: T[][] = [];
  let temp: T[] = [];
  for (let i = 0; i < arr.length; i++) {
    const tail = i % size;
    if (tail === 0) {
      temp = [];
      res.push(temp);
    }
    temp.push(arr[i]);
  }
  return res;
}

/**
 * 
 * @param arr
 * @param level
 * @returns
 */
export function flattern<K = any, T = any>(arr: T[], level = 1): K[] {
  if (level === 0) return arr as any;
  const res: K[] = [];
  for (let i = 0; i < arr.length; i++) {
    const value = arr[i];
    if (Array.isArray(value)) {
      flattern<K, T>(value, level - 1).forEach((t) => res.push(t));
    } else {
      res.push(value as any);
    }
  }
  return res;
}

/**
 * a2 a1
 * 
 * 1. a1 
 * 2. a1 
 * @param a1
 * @param a2
 * @returns
 */
export function mergeArray(a1: any[], a2: any[]) {
  const l1 = a1.length;
  const l2 = a2.length;
  const minL = min(l1, l2);
  for (let i = 0; i < minL; i++) {
    a1[i] = a2[i];
  }
  // a2
  if (l1 > minL) {
    for (let i = minL; i < l1; i++) {
      a1.pop();
    }
  }
  // a1
  if (l2 > minL) {
    for (let i = minL; i < l2; i++) {
      a1.push(a2[i]);
    }
  }
  return a1;
}
